import { graphql, Link, navigate } from "gatsby";
import React, { FunctionComponent } from "react";
import { MarkdownRemarkConnection, Site } from "../graphql-types";
import ReactPaginate from 'react-paginate'
import Styles from '../styles/templates/post-list.module.styl'
import Layout from "../components/layout";
import SEO from "../components/seo";

type Props = {
  data: {
    allMarkdownRemark: MarkdownRemarkConnection,
    site: Site
  },
  pageContext: {
    page: number
    limit: number,
    skip: number
  },
  location: Location
}

const monthInEng = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC']

const BlogPostList: FunctionComponent<Props> = props => {

  const { data } = props
  const { title: siteTitle } = data.site.siteMetadata
  const { edges: posts, totalCount } = data.allMarkdownRemark
  const { page, limit: size } = props.pageContext

  const pageCount = Math.ceil(totalCount / size)

  const handlePageChangeClick = (data: { selected: number }) => {
    const page = data.selected + 1
    if (page === 1) {
      navigate('/')
      return
    }

    navigate(`/posts/page/${page}`)
  }

  return (
    <Layout location={props.location} title={siteTitle}>
      <SEO title="All posts" />
      {posts.map(({ node }) => {
        const title = node.frontmatter.title || node.fields.slug
        return (
          <div className={Styles['post']} key={node.fields.slug}>
            <div className={Styles['date']}>
              <div className={Styles['up']}>
                <div className={Styles['month']}>{monthInEng[new Date(node.frontmatter.date).getMonth()]}</div>
                <div className={Styles['day']}>{new Date(node.frontmatter.date).getDay()}</div>
              </div>
              <div className={Styles['year']}>{new Date(node.frontmatter.date).getFullYear()}</div>
            </div>
            <div className={Styles['content']}>
              <div className={Styles['header']}>
                <Link className={Styles['title']} to={node.fields.slug}>
                  {title}
                </Link>
              </div>
              <div className={Styles['info']}>
                {
                  node.frontmatter.tags.map(tag => (
                    <React.Fragment key={tag}>
                      <i className='iconfont'>&#xe63e;</i>
                      <Link to='/tags' className={Styles['tag']}>{tag}</Link>
                      <span className={Styles['line']}>·</span>
                    </React.Fragment>
                  ))
                }
                {node.timeToRead + 1} min read
                  <span className={Styles['time']}>
                  <span className={Styles['line']}>·</span>
                  {new Date(node.frontmatter.date).getFullYear()}<span>年</span>
                  {new Date(node.frontmatter.date).getMonth() + 1}<span>月</span>
                  {new Date(node.frontmatter.date).getDay()}<span>日</span>
                </span>
              </div>
              <div className={Styles['brief']}>
                <p>
                  {node.frontmatter.description || node.excerpt}
                </p>
              </div>
              <Link to={node.fields.slug} className={Styles['more']}>Read More</Link>
            </div>
          </div>
        )
      })}
      <ReactPaginate
        pageCount={pageCount}
        pageRangeDisplayed={size}
        previousLabel={'←'}
        initialPage={page}
        nextLabel={'→'}
        onPageChange={handlePageChangeClick}
        containerClassName={Styles['pagination']}
      // breakClassName={Styles['li']}
      // breakLinkClassName={Styles['a']}
      />
    </Layout>
  )
}

export default BlogPostList

export const query = graphql`
	query PostListQuery($skip: Int, $limit: Int) {
    site {
      siteMetadata {
        title
      }
    }
		allMarkdownRemark(
			limit: $limit
			skip: $skip
			sort: { order: DESC, fields: frontmatter___date }
		) {
			totalCount
			edges {
				node {
					id
					frontmatter {
						date
						description
						tags
						title
          }
          fields {
            slug
          }
					fileAbsolutePath
					wordCount {
						words
					}
          timeToRead
				}
			}
		}
		dataYaml {
			createPageConfig {
				activeLayout
				posts {
					listPath
				}
			}
		}
	}
`;